.main {
  &__card {
    width: $card-width;
    box-sizing: border-box;
  }

  display: flex;
  justify-content: center;

  &__container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 48px;
    width: $card-width * 4 + 48px * 3;
    margin-bottom: 73px;
  }
}
